header {
  text-align: center;
  margin-bottom: 2rem; }
  header h1, header h3 {
    margin: 1rem; }
  header button {
    padding: 0.25rem 0.5rem; }

#board {
  height: 300px;
  width: 300px;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }
  @media screen and (min-width: 500px) {
    #board {
      height: 450px;
      width: 450px; } }
  #board .row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 0 0 33.33%;
            flex: 0 0 33.33%; }
    #board .row:not(:last-child) {
      border-bottom: 1px solid black; }
    #board .row .square {
      -webkit-flex: 0 0 33.33%;
              flex: 0 0 33.33%; }
      #board .row .square:not(:last-child) {
        border-right: 1px solid black; }
      #board .row .square button {
        width: 100%;
        height: 100%;
        border: none;
        background: none;
        font-size: 4.5rem; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
